import React from "react";
import styles from "./FindFriend.module.css";
import UserTiles from "../components/UserTiles";

function FindFriend({data}) {
  return (
    <div className={styles.container}>
      { data?
      data.map((user) => (<UserTiles key={user._id} userid={user._id} username={user.username} userimg={user.img}/>)):
      <h1> Find users by their usernames</h1>
      }
    </div>
  );
}

export default FindFriend;
