import "./App.css";
import React from "react";
import { BrowserRouter, Route } from "react-router-dom";
import Register from "./components/register/Register";
import Signin from "./components/signin/Signin";
import Home from "./components/home/Home/Home";
import ActiveHome from "./active/components/home/Home/ActiveHome.jsx";
import NotesHome from "./notes/components/home/Home/NotesHome.jsx";

function App() {
  return (
    <div>
      <BrowserRouter>
        <Route path="/active" exact component={ActiveHome}></Route>
        <Route path="/notes" exact component={NotesHome}></Route>
        <Route path="/register" exact component={Register}></Route>
        <Route path="/login" exact component={Signin}></Route>
        <Route path="/home" exact component={Home}></Route>

        {/* <Route path="/home" exact component={Home}></Route> */}
      </BrowserRouter>
    </div>
  );
}

export default App;
