import React, { useState, useEffect } from "react";
import styles from "../css/contactsWindow.module.css";
import Contacts from "./Contacts";
import SearchIcon from "@material-ui/icons/Search";
import FindFriend from "../FindFriend/FindFriend";
import axios from "axios";
import UserFriendRequests from "../UserProfile/UserFriendRequests";
import icon from "../public/add.png";
import uuid from "react-uuid";

function ContactsWindow({
  user,
  setuser,
  contacts,
  setContacts,
  printcontacts,
  currentchat,
  setCurrentchat,
}) {
  const [mode, setMode] = useState("CONT");
  const [searchbarval, setSearchbarval] = useState("");
  const [searchresult, setSearchresult] = useState();

  const setFindMode = () => {
    setSearchbarval("");
    setMode("FIND");
  };
  const setReqMode = () => {
    setSearchbarval("");
    setMode("REQ");
  };
  const setContMode = () => {
    setSearchbarval("");
    setMode("CONT");
    setCurrentchat(uuid());
  };

  useEffect(() => {
    console.log(searchbarval);
    //axios req
    if (searchbarval.length) {
      axios
        .get(`/friend/find/${searchbarval}`)
        .then((response) => {
          console.log(response);
          setSearchresult(response.data);
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      setSearchresult();
    }
  }, [searchbarval]);

  return (
    <div className={styles.container}>
      <div className={styles.head}>
        <div className={styles.headLeft}>
          {user.username && (
            <img src={user.img} alt="img" className={styles.userimage} />
          )}
          <div className={styles.userName}>{user.username}</div>
        </div>
      </div>
      <div className={styles.tabcontainer}>
        <div className={styles.tab} onClick={setContMode}>
          Posts
        </div>
        <div className={styles.tab} onClick={setFindMode}>
          Search
        </div>
        <div className={styles.tab} onClick={setReqMode}>
          Requests
        </div>
      </div>
      <div className={styles.searchContainer}>
        <div className={styles.search}>
          <input
            type="text"
            className={styles.searchBar}
            placeholder="Search for a contact"
            style={{ backgroundColor: "transparent" }}
            value={searchbarval}
            onChange={(e) => {
              setSearchbarval(e.target.value);
            }}
          />
        </div>
      </div>
      {mode === "CONT" && (
        <div style={{}}>
          <img src={icon} alt="plus" className={styles.plusicon} />
          <p className={styles.addpost}>Create Post</p>
        </div>
      )}
      {mode === "FIND" && <FindFriend data={searchresult} />}
      {mode === "REQ" && <UserFriendRequests user={user} setuser={setuser} />}
    </div>
  );
}

export default ContactsWindow;
