import React from "react";
import styles from "../css/message.module.css";
import DeleteIcon from "@mui/icons-material/Delete";

function Message(props) {
  return props.messageData.position === "center" ? (
    <div className={styles.containerCenter}>
      <div className={styles.messageTextCenter}>{props.messageData.text}</div>
    </div>
  ) : (
    <div
      className={
        props.messageData.position === "right"
          ? styles.containerRight
          : styles.containerLeft
      }
    >
      {/* <img
        src="https://media-eng.dhakatribune.com/uploads/2019/11/tahsan-web-1574170968575.gif"
        alt="senderImage"
        className={styles.image}
      /> */}
      <div className={styles.messageContainer}>
        <div className={styles.messageBy}>
          {props.messageData.commentedby} |
        </div>
        <div className={styles.messageText}>{props.messageData.text}</div>
      </div>
      {props.messageData.commenterid === props.user._id && (
        <DeleteIcon
          className={styles.sendIcon}
          onClick={() => props.deletecomment(props.messageData.id)}
          style={{ float: "right" }}
        />
      )}
    </div>
  );
}

export default Message;
