import React, { useState } from "react";
import IconButton from "@mui/material/IconButton";
import FavoriteIcon from "@mui/icons-material/Favorite";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import SendIcon from "@material-ui/icons/Send";
import Message from "./Message";
import uuid from "react-uuid";
import axios from "axios";
import DeleteIcon from "@mui/icons-material/Delete";
// import Button from '@material-ui/core/Button';
import styles from "../css/cards.module.css";
import { useEffect } from "react";

function Cards({ post, user }) {
  const [arrowbtnState, setarrowbtn] = useState(false);
  const [liked, setliked] = useState(post.likedby.includes(user._id));
  const [commentinput, setCommentinput] = useState("");
  const [rerender, setrerender] = useState(false);

  function changelikestatus() {
    if (liked) {
      post.likedby.splice(post.likedby.indexOf(user._id), 1);
      axios
        .post(`/post/unlike/${post._id}`)
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => console.log(err));
    } else {
      post.likedby.push(user._id);
      axios
        .post(`/post/like/${post._id}`)
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => console.log(err));
    }
    setliked(!liked);
  }

  function sendcomment() {
    if (commentinput !== "") {
      var uid = uuid();
      post.comments.push({
        id: uid,
        comment: commentinput,
        commentername: user.username,
        commenterid: user._id,
      });
      setCommentinput("");
      axios
        .post(`/post/addcomment/${post._id}`, {
          _id: uid,
          comment: commentinput,
        })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }

  function deletecomment(id) {
    console.log("deletecomment", id);
    post.comments = post.comments.filter((comment) => comment._id !== id);
    setrerender(!rerender);
    axios
      .delete(`/post/deletecomment/${post._id}/${id}`)
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  function deletepost() {
    axios
      .delete(`/post/delete/${post._id}`)
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  return (
    <div className={styles.body}>
      <div
        className={
          arrowbtnState
            ? `${styles.cardBodywithcomments}`
            : `${styles.cardBody}`
        }
      >
        <div className={styles.content}>
          <img src={post.uploaderimg} alt="img" className={styles.userimg} />
          <h1 className={styles.name}>{post.uploadername}</h1>
          {post.uploaderid === user._id && (
            <DeleteIcon
              className={styles.delIcon}
              onClick={deletepost}
              style={{ float: "right" }}
            />
          )}
          <img
            className={styles.cardImage}
            src={`http://localhost:2948/public/${post.name}`}
            alt="hello"
          />
          <div className={styles.cardContent}>
            <IconButton onClick={changelikestatus}>
              <FavoriteIcon
                className={liked ? `${styles.active} ` : `${styles.likeBtn}`}
              />
              <p className={styles.likes}>{post.likedby.length} likes</p>
            </IconButton>

            <div className={styles.comments}>
              <p className={styles.viewcomments}>View comments</p>
              {/* <ArrowDropDownIcon className={styles.arrow}/> */}
              <IconButton
                onClick={() => {
                  setarrowbtn(!arrowbtnState);
                }}
              >
                <ArrowDropDownIcon
                  style={{
                    fill: "rgb(224 4 158)",
                    transition: "0.3s ease-in-out",
                    transform: arrowbtnState
                      ? "rotate(180deg)"
                      : "rotate(0deg)",
                  }}
                />
              </IconButton>
            </div>
          </div>
          <div>
            <div
              style={{
                transition: "1s ease-in-out",
              }}
              className={
                arrowbtnState
                  ? `${styles.commentsdivhidden}`
                  : `${styles.commentsdiv}`
              }
            >
              {arrowbtnState && (
                <p className={styles.commentstitle}>Comments</p>
              )}
              {post.comments.length > 0 &&
                arrowbtnState &&
                post.comments.map(
                  (message) =>
                    message && (
                      <Message
                        messageData={{
                          text: message.comment,
                          position: "left",
                          commentedby: message.commentername,
                          commenterid: message.commenterid,
                          id: message._id,
                          key: message._id,
                        }}
                        key={message._id}
                        deletecomment={deletecomment}
                        user={user}
                      />
                    )
                )}
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  paddingBottom: "15px",
                }}
              >
                <input
                  type="text"
                  className={styles.input}
                  value={commentinput}
                  onChange={(e) => setCommentinput(e.target.value)}
                  style={{
                    display: arrowbtnState ? "flex" : "none",
                    cursor: "pointer",
                    transition: "0.3s ease-in-out",
                  }}
                  placeholder="Add a comment..."
                ></input>
                {arrowbtnState && (
                  <SendIcon className={styles.sendIcon} onClick={sendcomment} />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Cards;
