import React, { useState, useEffect } from "react";
import styles from "../css/chatWindow.module.css";
import MenuIcon from "@material-ui/icons/Menu";
import Message from "./Message";
import SendIcon from "@material-ui/icons/Send";
import axios from "axios";
import Cards from "./Cards.jsx";

import { io } from "socket.io-client";

function ChatWindow({ currentchat, user, setMode, mode }) {
  const [posts, setPosts] = useState([]);
  const [contacts, setContacts] = useState({});

  const sendpost = () => {
    const formData = new FormData();
    const imagefile = document.querySelector("#myFile");
    formData.append("myFile", imagefile.files[0]);
    console.log(formData, imagefile.files[0]);
    axios.post("/post/uploadFile", formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
  };

  function findUser(id) {
    for (var i = 0; i < contacts.length; i++) {
      if (contacts[i]._id === id) {
        console.log("find user", id);
        return contacts[i];
      }
    }
  }

  useEffect(() => {
    axios.get("/post/getAll").then((res) => {
      console.log(res.data);
      setPosts(res.data.posts);
    });
  }, []);

  function getContacts() {
    axios
      .post("/friend/findbyid", {
        requests: user.friends,
      })
      .then((response) => {
        setContacts(response.data);
        console.log("friends fetched too", response.data);
      })
      .catch((err) => {
        console.log("ERR", err);
      });
  }

  useEffect(() => {
    getContacts();
    //eslint-disable-next-line
  }, [user]);

  function changemode() {
    setMode(!mode);
  }

  return (
    <div className={styles.container}>
      <div className={styles.head}>
        <div className={styles.contactInfo}>
          <div className={styles.contactImage}>
            <img
              src={
                currentchat && currentchat.img !== ""
                  ? currentchat.img
                  : "https://iconape.com/wp-content/png_logo_vector/doraemon-logo.png"
              }
              alt="Contact Avatar"
            />
          </div>
          <div className={styles.contactDetail}>
            <div className={styles.contactName}>
              {currentchat ? currentchat.username : "Open a Chat"}
            </div>
          </div>
        </div>
        <MenuIcon className={styles.menuIcon} onClick={changemode} />
      </div>
      {/* <form
        action="/post/uploadFile"
        enctype="multipart/form-data"
        method="POST"
      >
        <input type="file" class="admin__input" id="myFile" name="myFile" />
        <input
          class="admin__submit"
          type="submit"
          onClick={(e) => {
            e.preventDefault();
            sendpost();
          }}
        />
      </form> */}
      {posts !== "" &&
        contacts.length > 0 &&
        posts.map((post) => (
          // post && (
          //   <img
          //     src={`http://localhost:2948/public/${post.name}`}
          //     alt="hello"
          //     style={{ width: "100px", height: "100px" }}
          //   />
          // )
          <Cards user={user} post={post} key={post._id} />
        ))}
      {/* {console.log("posts", posts.map)} */}

      <div>
        <input
          type="file"
          className="admin__input"
          id="myFile"
          name="myFile"
          // style={{ zIndex: "50", position: "absolute", top: "50px" }}
        />
        <SendIcon
          className={styles.sendIcon}
          onClick={sendpost}
          // style={{ zIndex: "50", position: "absolute", top: "80px" }}
        />
      </div>
    </div>
  );
}

export default ChatWindow;
